.section_bunner{
    align-items: center;
}


.bunner{
    margin: 0 auto;
    width: 90%;
    height: 400px;
    background-color: gray;
    border-radius: 25px;
}

.recomendations__h2{
    margin-top: 35px;
    margin-bottom: 30px;
}

.recomendations__card-wrap{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 30px;
    
}

.card{
    width: 100%;
    height: 300px;
    border: 1px solid #000000;
    border-radius: 10px;
}